<template>
    <div class="layout">
        <router-view></router-view>
    </div>
</template>
<script>
    import card from './components/card'
    export default {
        data () {
            return {
                isCollapsed: false
            }
        },
        components: {
            card
        },
        computed: {
            rotateIcon () {
                return ['menu-icon', this.isCollapsed ? 'rotate-icon' : '']
            },
            menuitemClasses () {
                return ['menu-item', this.isCollapsed ? 'collapsed-menu' : '']
            }
        },
        methods: {
            collapsedSider () {
                this.$refs.side1.toggleCollapse()
            }
        }
    }
</script>
<style lang="less">
    body {
        font-family: Helvetica Neue For Number,Chinese Quote,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,PingFang SC,Hiragino Sans GB,Microsoft YaHei,Helvetica Neue,Helvetica,Arial,sans-serif;
        font-size: 14px;
        line-height: 1.5;
        color: rgba(0,0,0,.65);
    }
    h1,h2,h3,h4,h5{
        font-weight: 500;
        color:#000;
    }
    .mt20 {
        margin-top: 24px;
    }
    .mr40{
        margin-right: 40px;
    }
    .mlr10 {
        margin-left:12px;
        margin-right:12px;
    }
    .mlr20 {
        margin-left:24px;
        margin-right:24px;
    }
    .plr10 {
        padding-left: 12px;
        padding-right: 12px;
    }
    .pt10 {
        padding-top: 12px;
    }
    .txt_center {
        text-align: center;
    }
    .fl {
        float: left;
    }
    .fr {
        float: right;
    }
    .clearfix {
        clear: both;
        ::after {
            content: '';
            zoom: 1;
        }
    }
    .top-key-name {
        padding-top: 24px;
        line-height: 1;
        padding-bottom: 12px;
    }
    .card_title {
        height: 50px;
        line-height: 50px;
        padding-left: 24px;
        font-size: 16px;
        color: #000;
        border-bottom:1px solid #e8e8e8;
    }
    .layout{
        background: #f5f7f9;
        position: absolute;
        overflow: hidden;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        .page-view {
            .page_breadcrumb {
                padding: 12px 24px;
            }
            .page_content {
                margin: 0 24px;
                background: #fff;
                padding-bottom: 50px;
            }
            &.full_page {
                height: 100%;
                display: flex;
                flex-direction: column;
                .page_content {
                    flex: 1;
                }
            }
        }
        .ivu-card-bordered {
            border: none;
        }
        .ivu-card {
            border-radius: 0;
        }
    }
</style>



