<template>
    <div>
        <Row class="plr10">
            <Col :xs="24" :sm="12" :md="6">
                <Card :dis-hover="true" class="mt20 mlr10">
                    <div>实时交易</div>
                    <div style="height:60px;"></div>
                    <div>
                        <div>日均销售额<span class="value">￥12,423</span></div>
                    </div>
                </Card>
            </Col>
            <Col :xs="24" :sm="12" :md="6">
                <Card :dis-hover="true" class="mt20 mlr10">
                    <div>实时交易</div>
                    <div style="height:60px;"></div>
                    <div>
                        <div>日均销售额<span class="value">￥12,423</span></div>
                    </div>
                </Card>
            </Col>
            <Col :xs="24" :sm="12" :md="6">
                <Card :dis-hover="true" class="mt20 mlr10">
                    <div>实时交易</div>
                    <div style="height:60px;"></div>
                    <div>
                        <div>日均销售额<span class="value">￥12,423</span></div>
                    </div>
                </Card>
            </Col>
            <Col :xs="24" :sm="12" :md="6">
                <Card :dis-hover="true" class="mt20 mlr10">
                    <div>实时交易</div>
                    <div style="height:60px;"></div>
                    <div class="flex-between">
                        <div>周同比<span class="value">12%</span></div>
                        <div>日环比<span class="value">11%</span></div>
                    </div>
                </Card>
            </Col>
        </Row>
        <Row class="plr10">
            <Col>
                <Card :bordered="false" :dis-hover="true" class="mt20 mlr10" :padding="0">
                    <div class="flex-between index_card">
                        <div class="left_tabs">
                            <span class="on">销售额</span>
                            <span>访问量</span>
                        </div>
                        <div class="right_data">
                            <a>今日</a>
                            <a>本周</a>
                            <a>本月</a>
                            <a>全年</a>
                            <DatePicker v-model="value7" @on-change="handleChange" type="daterange" placeholder="开始日期 - 结束日期" placement="bottom-end" style="width:200px"></DatePicker>
                        </div>
                    </div>
                    <div class="flex-between index_databar">
                        <div class="index_left_chart">
                            <h3>访问量趋势</h3>
                            <v-chart :force-fit="true" :height="254" :data="data3" :padding="[10,10,30,40]">
                                <v-tooltip />
                                <v-axis />
                                <v-legend />
                                <v-bar position="月份*月均降雨量" color="name" :adjust="adjust" />
                            </v-chart>
                        </div>
                        <div class="index_right_band">
                            <h3>门店访问量排名</h3>
                            <ul>
                                <li><a>1</a>工专路 0 号店<span>323,234</span></li>
                                <li><a>2</a>工专路 1 号店<span>323,234</span></li>
                                <li><a>3</a>工专路 2 号店<span>323,234</span></li>
                                <li><a>4</a>工专路 3 号店<span>323,234</span></li>
                                <li><a>5</a>工专路 4 号店<span>323,234</span></li>
                                <li><a>6</a>工专路 5 号店<span>323,234</span></li>
                                <li><a>7</a>工专路 6 号店<span>323,234</span></li>
                            </ul>
                        </div>
                    </div>
                </Card>
            </Col>
        </Row>
        <Row class="plr10">
            <Col :xs="24" :sm="12" :md="16">
                <Card :dis-hover="true" :padding="0" class="mt20 mlr10">
                    <div class="card_title">实时交易</div>
                    <div>
                        <v-chart :force-fit="true" :height="350" :data="data1" :scale="scale" :padding="[40,40,60,60]">
                            <v-tooltip />
                            <v-axis />
                            <v-line :position="'year*value'" />
                            <v-point :position="'year*value'" />
                        </v-chart>
                    </div>
                </Card>
            </Col>
            <Col :xs="24" :sm="12" :md="8">
                <Card :dis-hover="true" :padding="0" class="mt20 mlr10">
                    <div class="card_title">实时交易</div>
                    <div>
                        <v-chart :force-fit="true" :height="350" :data="data4" :scale="scale4" :padding="[40,0,40,0]">
                            <v-tooltip :show-title="false" :data-key="'item*percent'" />
                            <v-axis />
                            <v-legend :data-key="'item'" />
                            <v-pie :position="'percent'" :color="'item'" :v-style="pieStyle4" :label="labelConfig4"/>
                            <v-coord :type="'theta'" />
                        </v-chart>
                    </div>
                </Card>
            </Col>
        </Row>
        <Row class="plr10">
            <Col :xs="24" :sm="12" :md="12">
                <Card :dis-hover="true" :padding="0" class="mt20 mlr10">
                    <div>
                        <div class="card_title">实时交易</div>
                        <v-chart :force-fit="true" height="400" :data="data2" :scale="scale2" :padding="[50,0,80,0]">
                            <v-tooltip :show-title="false" :data-key="'item*percent'"/>
                            <v-axis />
                            <v-legend :data-key="'item'"/>
                            <v-pie :position="'percent'" :color="'item'" :v-style="pieStyle" :label="labelConfig"/>
                            <v-coord :type="'theta'" />
                        </v-chart>
                    </div>
                </Card>
            </Col>
            <Col :xs="24" :sm="12" :md="12">
                <Card :dis-hover="true" :padding="0" class="mt20 mlr10">
                    <div>
                        <div class="card_title">实时交易</div>
                        <v-chart force-fit="true" height="400" :data="data5" :scale="scale5" :padding="[10,0,10,0]">
                            <v-tooltip />
                            <v-axis :dataKey="axis1Opts5.dataKey" :line="axis1Opts5.line" :tickLine="axis1Opts5.tickLine" :grid="axis1Opts5.grid" />
                            <v-axis :dataKey="axis2Opts5.dataKey" :line="axis2Opts5.line" :tickLine="axis2Opts5.tickLine" :grid="axis2Opts5.grid" />
                            <v-legend data-key="user" marker="circle" offset="30" />
                            <v-coord type="polar" radius="0.8" />
                            <v-line position="item*score" color="user" size="2" />
                            <v-point position="item*score" color="user" size="4" />
                            <v-area position="item*score" color="user" />
                        </v-chart>
                    </div>
                </Card>
            </Col>
        </Row>
    </div>
</template>
<script>
    // import card from '../components/card'
    const data1 = [{ year: '1991', value: 3 }, { year: '1992', value: 4 }, { year: '1993', value: 3.5 }, { year: '1994', value: 5 }, { year: '1995', value: 4.9 }, { year: '1996', value: 6 }, { year: '1997', value: 7 }, { year: '1998', value: 9 }, { year: '1999', value: 13 }]

    const scale = [{
        dataKey: 'value',
        min: 0
    }, {
        dataKey: 'year',
        min: 0,
        max: 1
    }]
    const DataSet = require('@antv/data-set')

    const sourceData = [
        { item: '事例一', count: 40 },
        { item: '事例二', count: 21 },
        { item: '事例三', count: 17 },
        { item: '事例四', count: 13 },
        { item: '事例五', count: 9 }
    ]

    const scale2 = [{
        dataKey: 'percent',
        min: 0,
        formatter: '.0%'
    }]

    const dv = new DataSet.View().source(sourceData)
    dv.transform({
        type: 'percent',
        field: 'count',
        dimension: 'item',
        as: 'percent'
    })
    const data2 = dv.rows

    const sourceData3 = [
        { name: 'London', '一月': 18.9, '二月': 28.8, '三月': 39.3, '四月': 110, '五月': 47, '六月': 20.3, '七月': 24, '八月': 35.6, '九月': 39, '十月': 38, '十一月': 28, '十二月': 32 },
        { name: 'Berlin', '一月': 12.4, '二月': 23.2, '三月': 34.5, '四月': 99.7, '五月': 52.6, '六月': 35.5, '七月': 37.4, '八月': 42.4, '九月': 39, '十月': 38, '十一月': 28, '十二月': 32 }
    ]
    const dv3 = new DataSet.View().source(sourceData3)
    dv3.transform({
        type: 'fold',
        fields: ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月'],
        key: '月份',
        value: '月均降雨量'
    })
    const data3 = dv3.rows

    const sourceData4 = [
        { item: '事例一', count: 40 },
        { item: '事例二', count: 21 },
        { item: '事例三', count: 17 },
        { item: '事例四', count: 13 },
        { item: '事例五', count: 9 }
    ]

    const scale4 = [{
        dataKey: 'percent',
        min: 0,
        formatter: '.0%'
    }]

    const dv4 = new DataSet.View().source(sourceData4)
    dv4.transform({
        type: 'percent',
        field: 'count',
        dimension: 'item',
        as: 'percent'
    })
    const data4 = dv4.rows

    const sourceData5 = [
        { item: 'Design', a: 70, b: 30 },
        { item: 'Development', a: 60, b: 70 },
        { item: 'Marketing', a: 50, b: 60 },
        { item: 'Users', a: 40, b: 50 },
        { item: 'Test', a: 60, b: 70 },
        { item: 'Language', a: 70, b: 50 },
        { item: 'Technology', a: 50, b: 40 },
        { item: 'Support', a: 30, b: 40 },
        { item: 'Sales', a: 60, b: 40 },
        { item: 'UX', a: 50, b: 60 }
    ]

    const dv5 = new DataSet.View().source(sourceData5)
    dv5.transform({
        type: 'fold',
        fields: ['a', 'b'],
        key: 'user',
        value: 'score'
    })
    const data5 = dv5.rows

    const scale5 = [{
        dataKey: 'score',
        min: 0,
        max: 80
    }]

    const axis1Opts5 = {
        dataKey: 'item',
        line: null,
        tickLine: null,
        grid: {
            lineStyle: {
                lineDash: null
            },
            hideFirstLine: false
        }
    }
    const axis2Opts5 = {
        dataKey: 'score',
        line: null,
        tickLine: null,
        grid: {
            type: 'polygon',
            lineStyle: {
                lineDash: null
            }
        }
    }
    export default {
        methods: {
            handleChange (v) {
                console.log(v)
            }
        },
        data () {
            return {
                value7: '',
                data1,
                scale,
                height: 400,
                data2,
                scale2,
                height2: 400,
                pieStyle: {
                    stroke: '#fff',
                    lineWidth: 1
                },
                labelConfig: ['percent', {
                    formatter: (val, item) => {
                        return item.point.item + ': ' + val
                    }
                }],
                data3,
                height3: 300,
                adjust: [{
                    type: 'dodge',
                    marginRatio: 1 / 32
                }],
                data4,
                scale4,
                height4: 300,
                pieStyle4: {
                    stroke: '#fff',
                    lineWidth: 1
                },
                labelConfig4: ['percent', {
                    offset: -40,
                    textStyle: {
                        rotate: 0,
                        textAlign: 'center',
                        shadowBlur: 2,
                        shadowColor: 'rgba(0, 0, 0, .45)'
                    }
                }],
                data5,
                scale5,
                padding5: [20, 20, 95, 20],
                axis1Opts5,
                axis2Opts5
            }
        }
    }
</script>
<style lang="less">
    .index_card {
        border-bottom:1px solid #e8e8e8;
        .left_tabs {
            color: #000;
            font-size: 16px;
            line-height: 50px;
            span {
                margin-left: 24px;
                position: relative;
                &.on {
                    color: #2d8cf0;
                    &:after{
                        border-bottom: 2px solid #2d8cf0;
                        content: '';
                        position: absolute;
                        left: 0;
                        bottom: -17px;
                        width: 100%;
                    }
                }
            }
        }
        .right_data {
            padding-top: 10px;
            padding-right: 24px;
            a {
                padding-right: 12px;
            }
        }
    }
    .index_databar {
        .index_left_chart {
            margin: 24px 0 32px 32px;
            flex: 1;
        }
        .index_right_band {
            margin: 24px 32px 32px 60px;
            width: 350px;
            li {
                margin-top: 16px;
                font-size: 14px;
                color: rgba(0,0,0,.65);
                a {
                    display: inline-block;
                    border-radius: 50%;
                    margin-right: 20px;
                }
                span {
                    float: right;
                }
            }
        }
        h3 {
            margin-bottom: 24px;
        }
    }
</style>


