                                <span slot="prepend">http://</span>
                                <span slot="append">.com</span>
                            </Input>
                        </Col>
                        <Col span="8">
                            <p class="top-key-name">仓库管理员</p>
                            <Select placeholder="请选择管理员">
                                <Option :value="1">xxx</Option>
                                <Option :value="1">xxx</Option>
                            </Select>
                        </Col>
                        <Col span="8">
                            <p class="top-key-name">审批人</p>
                            <Select placeholder="请选择审批员">
                                <Option :value="1">xxx</Option>
                                <Option :value="1">xxx</Option>
                            </Select>
                        </Col>
                        <Col span="8">
                            <p class="top-key-name">生效日期</p>
                            <DatePicker type="daterange" placement="bottom-end" placeholder="开始日期   ~   结束日期" style="width:100%"></DatePicker>
                        </Col>
                        <Col span="8">
                            <p class="top-key-name">仓库类型</p>
                            <Select placeholder="请选择仓库类型">
                                <Option :value="1">xxx</Option>
                                <Option :value="1">xxx</Option>
                            </Select>
                        </Col>
                    </Row>
                </div>
            </Card>
        </div>
        <div>
            <Card :dis-hover="true" :padding="0" class="mt20 mlr20">
                <div class="card_title">任务管理</div>
                <div style="padding:0 30px 40px;">
                    <Row :gutter="40">
                        <Col span="8">
                            <p class="top-key-name">任务名</p>
                            <Input placeholder="请输入"></Input>
                        </Col>
                        <Col span="8">
                            <p class="top-key-name">任务描述</p>
                            <Input placeholder="请输入"></Input>
                        </Col>
                        <Col span="8">
                            <p class="top-key-name">执行人</p>
                            <Input placeholder="请选择管理员"></Input>
                        </Col>
                        <Col span="8">
                            <p class="top-key-name">责任人</p>
                            <Input placeholder="请输入"></Input>
                        </Col>
                        <Col span="8">
                            <p class="top-key-name">生效日期</p>
                            <Input placeholder="提醒时间"></Input>
                        </Col>
                        <Col span="8">
                            <p class="top-key-name">任务类型</p>
                            <Input placeholder="请选择仓库类型"></Input>
                        </Col>
                    </Row>
                </div>
            </Card>
        </div>
        <div>
            <Card :dis-hover="true" :padding="0" class="mt20 mlr20">
                <div class="card_title">成员管理</div>
