<template>
    <div class="page-login">
        <div class="login-box p-center">
            <div class="login-box-title">
                <img src="../assets/logo.svg" alt="">
                <span>系统登录</span>
            </div>
            <div class="input-warrp">
                <Input v-model="value1" placeholder="账号" style="width:245px">
                    <Icon type="ios-person-outline" slot="prepend" style="width:16px;"></Icon>
                </Input>
            </div>
            <div class="input-warrp">
                <Input v-model="value2" placeholder="密码" style="width:245px">
                    <Icon type="ios-locked-outline" slot="prepend" style="width:16px;"></Icon>
                </Input>
            </div>
            <div class="input-warrp">
                <Input v-model="value3" placeholder="验证码" style="width:100px"></Input>
            </div>
            <div class="input-warrp">
                <button class="login-button" @click="toIndex">登录</button>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                value1: '',
                value2: '',
                value3: ''
            }
        },
        methods: {
            toIndex () {
                this.$router.push({path: '/main/index'})
                this.$Message.info('登陆成功')
            }
        }
    }
</script>

<style lang="less">
.page-login {
    height: 100%;
    width: 100%;
    position: relative;
    background: #fff;
}
.login-box {
    margin-top: -50px;
    text-align: center;
    background: #fff;
    padding: 40px 40px 60px;
    box-shadow: 0 0 100px rgba(0,0,0,.08);
    .login-box-title {
        font-size: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding-bottom: 10px;
        img {
            width: 30px;
            margin-right: 20px;
        }
    }
    .input-warrp {
        text-align: left;
        margin-top: 20px;
        .f {
            width: 100%;
        }
        .h {
            width: 100px;
        }
    }
    .login-button {
        width: 100%;
        border: none;
        background: rgb(39, 137, 228);
        color: #fff;
    }
}

</style>




