<template>
    <div class="page-view full_page">
        <div class="page_breadcrumb">
            <Breadcrumb>
                <BreadcrumbItem to="/">首页</BreadcrumbItem>
                <BreadcrumbItem>基本表单</BreadcrumbItem>
            </Breadcrumb>
        </div>
        <div class="page_content">
            <Form class="data_add" :model="formItem" :label-width="100">
                <FormItem label="标题">
                    <Input v-model="formItem.input" placeholder="给目标起个名字"></Input>
                </FormItem>
                <FormItem label="起止日期">
                    <DatePicker :value="formItem.date" format="yyyy.MM.dd" type="daterange" placement="bottom-start" placeholder="开始日期  ~   结束日期"></DatePicker>
                </FormItem>
                <FormItem label="目标描述">
                    <Input v-model="formItem.textarea" type="textarea" :autosize="{minRows: 3,maxRows: 5}" placeholder="请输入你的阶段性工作目标"></Input>
                </FormItem>
                <FormItem label="衡量标准">
                    <Input v-model="formItem.textarea" type="textarea" :autosize="{minRows: 3,maxRows: 5}" placeholder="请输入衡量标准"></Input>
                </FormItem>
                <FormItem label="客户">
                    <Input v-model="formItem.input" placeholder="请描述你服务的客户，内部客户直接 @姓名／工号"></Input>
                </FormItem>
                <FormItem label="邀评人">
                    <Input v-model="formItem.input" placeholder="请直接 @姓名／工号，最多可邀请 5 人"></Input>
                </FormItem>
                <FormItem label="权重">
                    <InputNumber :max="10" :min="1" :step="1.2" placeholder="请输入"  v-model="formItem.value7"></InputNumber> %
                </FormItem>
                <FormItem label="目标公开">
                    <RadioGroup v-model="formItem.radio">
                        <Radio label="a">公开</Radio>
                        <Radio label="b">部分公开</Radio>
                        <Radio label="f">不公开</Radio>
                    </RadioGroup>
                    <p style="color:#999">客户、邀评人默认被分享</p>
                </FormItem>
                <FormItem>
                    <Button type="primary">提 交</Button>
                    <Button type="ghost" style="margin-left: 8px">保 存</Button>
                </FormItem>
            </Form>
        </div>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                formItem: {
                    input: '',
                    select: '',
                    radio: 'male',
                    checkbox: [],
                    switch: true,
                    date: '',
                    time: '',
                    slider: [20, 50],
                    textarea: '',
                    value7: 0
                }
            }
        }
    }
</script>

<style lang="less">
    .data_add {
        padding-top: 100px;
        padding-bottom: 30px;
        width: 500px;
        margin: 0 auto;
        transform: translateX(-50px)
    }
</style>



