<template>
    <div class="layout-left-menu menu-content">
        <div class="logo-box">
            <img src="../assets/logo.svg" alt="">
            <span>管理系统</span>
        </div>
        <Menu width="auto" :accordion=true :active-name="nameActive" :open-names="nameOpen" @on-select="miclick">
            <MenuItem name="1">
                <Icon type="ios-paper"></Icon>管理平台
            </MenuItem>
            <MenuItem name="8">
                <Icon type="stats-bars"></Icon>系统管理
            </MenuItem>
            <Submenu name="2">
                <template slot="title">
                    <Icon type="ios-people"></Icon>表单页面
                </template>
                <MenuItem name="2-1">基础表单</MenuItem>
                <MenuItem name="2-2">分步表单</MenuItem>
                <MenuItem name="2-3">高级表单</MenuItem>
            </Submenu>
            <Submenu name="3">
                <template slot="title">
                    <Icon type="stats-bars"></Icon>列表页面
                </template>
                <MenuItem name="3-1">搜索项目</MenuItem>
                <MenuItem name="3-2">搜索应用</MenuItem>
                <MenuItem name="3-3">搜索文章</MenuItem>
                <MenuItem name="3-4">查询表格</MenuItem>
                <MenuItem name="3-5">标准列表</MenuItem>
                <MenuItem name="3-6">卡片列表</MenuItem>
            </Submenu>
            <Submenu name="4">
                <template slot="title">
                    <Icon type="stats-bars"></Icon>详情页面
                </template>
                <MenuItem name="4-1">基础详情</MenuItem>
                <MenuItem name="4-2">高级详情</MenuItem>
            </Submenu>
            <Submenu name="5">
                <template slot="title">
                    <Icon type="stats-bars"></Icon>结果页面
                </template>
                <MenuItem name="5-1">处理成功</MenuItem>
                <MenuItem name="5-2">处理失败</MenuItem>
            </Submenu>
            <MenuItem name="7">
                <Icon type="stats-bars"></Icon>用户管理
            </MenuItem>
            <MenuItem name="9">
                <Icon type="stats-bars"></Icon>公共组件
            </MenuItem>
            <MenuItem name="10">
                <Icon type="stats-bars"></Icon>测试导航
            </MenuItem>
            <MenuItem name="12">
                <Icon type="stats-bars"></Icon>异常页面
            </MenuItem>
        </Menu>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                ss: '',
                nameActive: '',
                nameOpen: [],
                routerMap: {
                    '1': '/main/index',
                    '1-2': '/main/index2',
                    '1-3': '/main/index3',
                    '2-1': '/form/basic',
                    '2-2': '/form/step',
                    '2-3': '/form/advanced',
                    '3-1': '/list/projects',
                    '3-2': '/list/applications',
                    '3-3': '/list/articles',
                    '3-4': '/list/table-list',
                    '3-5': '/list/basic-list',
                    '3-6': '/list/card-list',
                    '4-1': '/profile/basic',
                    '4-2': '/profile/advanced',
                    '5-1': '/result/success',
                    '5-2': '/result/fail',
                    '9': '/main/components'
                }
            }
        },
        created () {
            for (var key in this.routerMap) {
                if (this.routerMap[key] === this.$router.currentRoute.path) {
                    this.nameActive = key
                    this.nameOpen[0] = key.split('-')[0]
                }
            }
        },
        methods: {
            miclick (str) {
                this.$router.push({path: this.routerMap[str]})
            }
        }
    }
</script>

<style lang="less">
    .layout-left-menu {
        overflow: auto;
        width: 250px;
        background: #1f1f2a;
        border-right: 1px solid #efefef;
        color: #b4b8cc;
        height: 100%;
        &::-webkit-scrollbar {
            position: absolute;
            width:0px;
            height:10px;
        }
    }
    .logo-box {
        display: flex;
        font-size: 25px;
        padding-left: 25px;
        // background: #29293a;
        align-items: center;
        height: 130px;
        img {
            width: 40px;
            margin-right: 20px;
        }
    }
    .ivu-menu {
        .ivu-menu-item>i {
            margin-right: 8px;
        }
        .ivu-menu-submenu-title span>i {
            margin-right: 8px;
        } 
        .ivu-menu-submenu-title>i {
            margin-right: 8px;
        }
        color: #b5bed4;
        .ivu-menu-item, .ivu-menu-submenu-title {
            padding-left: 34px;
        }
        &.ivu-menu-vertical {
            .ivu-menu-item {
                &:hover {
                    background: #14141b;
                }
            }
            .ivu-menu-submenu {
                .ivu-menu-item {
                    padding-left: 52px;
                }
            }
            .ivu-menu-submenu-title {
                &:hover {
                    background: #14141b;
                }
            }
            &.ivu-menu-light {
                background: transparent;
                &:after {
                    background: none;
                }
                .ivu-menu-item-active:not(.ivu-menu-submenu) {
                    border-right: 5px solid #2d8cf0;
                }
            }
        }
        .ivu-menu-opened {
            background: #14141b;
            .ivu-menu {
                background: #14141b;
            }
        }
    }
</style>



