<template>
    <div class="page-view">
        <div class="page_breadcrumb">
            <Breadcrumb>
                <BreadcrumbItem to="/">首页</BreadcrumbItem>
                <BreadcrumbItem>UI组件</BreadcrumbItem>
            </Breadcrumb>
        </div>
        <div class="page_content">
            <!-- <al-cascader v-model="resArr" /> -->
            <div class="p20">
                <p>级联选择</p>               
                <Cascader :data="data" v-model="value1" style="width:200px;"></Cascader>
                <br>
                <p>级联选择</p>               
                <DatePicker v-model="value7" @on-change="handleChange" type="datetimerange" placeholder="Select date" style="width: 400px"></DatePicker>
                <br>
                <br>
                <DatePicker v-model="value6" type="datetime" @on-change="handleChange" style="width: 200px"></DatePicker>
                <br>
                <br>
                <TimePicker type="time" placeholder="Select time" style="width: 168px"></TimePicker>
                {{ value7 }}
                {{ value6 }}
                <br>
                <br>
                <DatePicker type="year" v-model="value5" placeholder="Select year" style="width: 200px"></DatePicker>
                <br>
                <br>
                <DatePicker type="month" v-model="value4" placeholder="Select month" style="width: 200px"></DatePicker>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        methods: {
            handleChange (v) {
                console.log(v)
            }
        },
        data () {
            return {
                value7: ['2018-03-05 10:00:00', '2018-05-15 10:01:00'],
                value6: '2018-02-05 10:09:00',
                value5: '1978',
                value4: '1978-05',
                value1: [],
                data: [{
                    value: 'beijing',
                    label: '北京',
                    children: [
                        {
                            value: 'gugong',
                            label: '故宫'
                        },
                        {
                            value: 'tiantan',
                            label: '天坛'
                        },
                        {
                            value: 'wangfujing',
                            label: '王府井'
                        }
                    ]
                }, {
                    value: 'jiangsu',
                    label: '江苏',
                    children: [
                        {
                            value: 'nanjing',
                            label: '南京',
                            children: [
                                {
                                    value: 'fuzimiao',
                                    label: '夫子庙'
                                }
                            ]
                        },
                        {
                            value: 'suzhou',
                            label: '苏州',
                            children: [
                                {
                                    value: 'zhuozhengyuan',
                                    label: '拙政园'
                                },
                                {
                                    value: 'shizilin',
                                    label: '狮子林'
                                }
                            ]
                        }
                    ]
                }]
            }
        }
    }
</script>

<style lang="less">
    .data_add {
        padding-top: 100px;
        padding-bottom: 30px;
        width: 500px;
        margin: 0 auto;
    }
</style>


