import Vue from 'vue'
import Router from 'vue-router'
import main from '@/components/maincontent'
import login from '@/view/login'
// 首页
import index from '@/view/index'
import index2 from '@/view/index2'
import index3 from '@/view/index3'
// 表单页
import formBasic from '@/view/form_basic'
import formStep from '@/view/form_step'
import formAdvanced from '@/view/form_advanced'
// 列表页
import projects from '@/view/list_projects'
import applications from '@/view/list_applications'
import articles from '@/view/list_articles'
import tableList from '@/view/list_tableList'
import basicList from '@/view/list_basicList'
import cardList from '@/view/list_cardList'
// 详情页
import profileBasic from '@/view/profile_basic'
import profileAdvanced from '@/view/profile_advanced'
// 结果页
import success from '@/view/result_success'
import fail from '@/view/result_fail'

import uicomponents from '@/view/ui_component'
Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'login',
        component: login
    }, {
        path: '/main',
        name: 'main',
        component: main,
        children: [
            {
                path: 'index',
                component: index
            }, {
                path: 'index2',
                component: index2
            }, {
                path: 'index3',
                component: index3
            }
        ]
    }, {
        path: '/form',
        component: main,
        children: [
            {
                path: 'basic',
                component: formBasic
            }, {
                path: 'step',
                component: formStep
            }, {
                path: 'advanced',
                component: formAdvanced
            }
        ]
    }, {
        path: '/list',
        component: main,
        children: [
            {
                path: 'projects',
                component: projects
            }, {
                path: 'applications',
                component: applications
            }, {
                path: 'articles',
                component: articles
            }, {
                path: 'table-list',
                component: tableList
            }, {
                path: 'basic-list',
                component: basicList
            }, {
                path: 'card-list',
                component: cardList
            }
        ]
    }, {
        path: '/profile',
        component: main,
        children: [
            {
                path: 'basic',
                component: profileBasic
            }, {
                path: 'advanced',
                component: profileAdvanced
            }
        ]
    }, {
        path: '/result',
        component: main,
        children: [
            {
                path: 'success',
                component: success
            }, {
                path: 'fail',
                component: fail
            }
        ]
    }, {
        path: '/main',
        component: main,
        children: [
            {
                path: 'components',
                component: uicomponents
            }
        ]
    }]
})



