<template>
    <div class="col-item" :class="'col-item-' + size">
        <div class="card" :class="hover ? 'hover' : ''">
            <p class="card-title" v-if="title">{{title}}</p>
            <div class="card-content">
                <slot name="body"></slot>
            </div>
            <div class="card-foot">
                <slot name="foot"></slot>
            </div>
        </div>
    </div>
</template>
<script type="text/javascript">
    export default {
        props: {
            hover: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: ''
            },
            size: {
                type: String,
                default: '1'
            }
        }
    }
</script>
<style lang="less">
    .inset .card {
        padding:20px;
    }
    .card {
        background: #fff;
        transition: all .3s;
        position: relative;
        flex-direction: column;
        min-width: 0;
        word-wrap: break-word;
        background-color: #fff;
        &.hover{
            &:hover {
                transition: 300ms;
                transform: translateY(-2px);
                box-shadow: 0 6px 12px rgba(84, 48, 132, 0.03);
            }
        }
        .card-title {
            font-size: 14px;
            color: rgba(0, 0, 0, 0.45);
        }
        .card-content {
            min-height: 50px;
        }
        .card-foot .value {
            color: #333;
            margin-left: 5px;
        }
    }
</style>



