<template>
    <div class="page-fill layout-warp">
        <layoutLeft></layoutLeft>
        <layoutRight>
            <topBar></topBar>
            <div class="main-content">
                <router-view></router-view>
            </div>
        </layoutRight>
    </div>
</template>
<script>
    import topBar from '../components/topbar'
    import layoutLeft from '../components/layoutLeft'
    import layoutRight from '../components/layoutRight'
    export default {
        components: {
            layoutLeft,
            layoutRight,
            topBar
        }
    }
</script>

<style lang="less">
    .main-content {
        // box-shadow: 0 0 10px rgba(229, 229, 229, 0.5) inset;
        flex: 1;
        overflow: hidden;
        overflow-y: auto;
        box-sizing: border-box;
        padding-bottom: 24px;
    }
</style>



