<template>
    <div class="top-bar">
        <div class="left-control" @click="hiddenMenu">
            <Icon type="navicon-round" size="30"></Icon>
        </div>
        <div style="margin-right:20px;">
            <Dropdown placement="bottom-end">
                <div class="user-box">
                    <div class="user-photo link">
                        <img src="https://avatars2.githubusercontent.com/u/9925435?v=4&s=400&u=0a2b1bbb9e804619070e9f020f34db30f63d7902">
                    </div>
                    <div>管理员</div>
                </div>
                <DropdownMenu slot="list">
                    <DropdownItem style="text-align:center">修改密码</DropdownItem>
                    <DropdownItem style="text-align:center">系统设置</DropdownItem>
                    <DropdownItem style="text-align:center" @click="$router.push({parh:'/'})">退出系统</DropdownItem>
                </DropdownMenu>
            </Dropdown>
        </div>
    </div>
</template>
<script>
    export default {
        methods: {
            hiddenMenu () {},
            ok () {
                this.$Message.info('点击了确定')
            },
            cancel () {
                this.$Message.info('点击了取消')
            }
        }
    }
</script>
<style lang="less">
    .top-bar {
        display: flex;
        justify-content: space-between;
        height: 64px;
        background: #fff;
        // box-shadow: 0 3px 10px rgba(84, 48, 132, 0.03);
        border-bottom: 1px solid #eee;
        position: relative;
        z-index: 5;
        .left-control {
            display: block;
            width: 64px;
            height: 64px;
            line-height: 64px;
            text-align: center;
            transition: 300ms;
            box-sizing: border-box;
            padding-top: 5px;
            &:hover {
                background: #eee;
            }
        }
        .user-box {
            margin: 12px 0px;
            display: flex;
            align-items: center;
            line-height: 1;
            .user-photo {
                width: 40px;
                height: 40px;
                margin-right: 10px;
            }
        }
    }
</style>



