<template>
    <div class="page-view full_page">
        <div class="page_breadcrumb">
            <Breadcrumb>
                <BreadcrumbItem to="/">首页</BreadcrumbItem>
                <BreadcrumbItem>分步表单</BreadcrumbItem>
