            <div style="min-height:400px;width:500px;margin:auto;padding-top:50px;">
                <div v-show = "stepIndex === 0">
                    <Form ref="formValidate" :label-width="80" style="width:400px;">
                        <FormItem label="付款账户" prop="city">
                            <Select placeholder="请选择账户">
                                <Option value="ant-design@alipay.com">ant-design@alipay.com</Option>
                            </Select>
                        </FormItem>
                        <FormItem label="收款账户" prop="name">
                            <Input placeholder="输入账号">
                                <Select v-model="select1" slot="prepend" style="width: 80px">
                                    <Option value="http">支付宝</Option>
                                    <Option value="https">银行账户</Option>
                                </Select>
                            </Input>
                        </FormItem>
                        <FormItem label="收款人姓名" prop="name">
                            <Input placeholder="输入收款人姓名"></Input>
                        </FormItem>
                        <FormItem label="转账金额" prop="name">
                            <Input placeholder="输入转账金额"></Input>
                        </FormItem>
                        <FormItem>
                            <Button type="primary" @click="handleSubmit(1)">下一步</Button>
                        </FormItem>
                    </Form>
                </div>
                <div v-show = "stepIndex === 1">
                    <Form ref="formValidate" :label-width="100">
                        <FormItem label="付款账户">
                            ant-design@alipay.com
                        </FormItem>
                        <FormItem label="收款账户">
                            test@example.com
                        </FormItem>
                        <FormItem label="收款人姓名">
                            Alex
                        </FormItem>
                        <FormItem label="转账金额">
                            500（伍佰元整）
                        </FormItem>
                        <FormItem label="支付密码" prop="password">
                            <Input placeholder="支付密码" style="width:200px;"></Input>
                        </FormItem>
                        <FormItem>
                            <Button type="primary"  @click="handleSubmit(2)">提交</Button>
                            <Button type="ghost" @click="handleSubmit(0)" style="margin-left: 10px">上一步</Button>
                        </FormItem>
                    </Form>
                </div>
                <div v-show = "stepIndex === 2">
                    <div slot="body">
                        <div class="result" style="padding-top: 48px; margin-bottom: 100px;text-align:center;">
                            <Icon type="checkmark-circled" :size="80" color="#52c41a"></Icon>
                            <div class="title34" style="font-size:32px;margin-top:20px;">提交成功</div>
                            <div style="font-size: 14px;line-height: 22px;color: rgba(0, 0, 0, 0.45);margin-bottom: 24px;"> 
                                预计两小时内到账
                            </div>
                            <div style="background: #fafafa;padding: 24px 40px;border-radius: 2px;text-align: left;">
                                <p>付款账户：ant-design@alipay.com</p>
                                <p>收款账户：test@example.com</p>
                                <p>收款人姓名：Alex</p>
                                <p>转账金额：500 元</p>
                            </div>
                            <div style="margin-top:30px;">
                                <Button type="primary" @click="handleSubmit(0)">再转一笔</Button>
                                <Button type="ghost" style="margin-left: 8px">查看账单</Button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div v-show="stepIndex==0" style="color:rgba(0,0,0,.45);border-top:1px solid #e8e8e8; margin:0 30px;line-height:25px;">
                <p style="font-size:18px;margin-top:30px">说明</p>
                <p style="margin-top:10px">转账到支付宝账户</p>
                <p>如果需要，这里可以放一些关于产品的常见问题说明。如果需要，这里可以放一些关于产品的常见问题说明。如果需要，这里可以放一些关于产品的常见问题说明。</p>
                <p style="margin-top:10px">转账到银行卡</p>
                <p>如果需要，这里可以放一些关于产品的常见问题说明。如果需要，这里可以放一些关于产品的常见问题说明。如果需要，这里可以放一些关于产品的常见问题说明。</p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                stepIndex: 0,
                select1: ''
            }
        },
        methods: {
            handleSubmit (val) {
                this.stepIndex = val
            },
            handleReset (name) {
                this.$refs[name].resetFields()
            }
        }
    }
</script>



