<template>
    <div class="page-view">
        <div class="page_breadcrumb">
            <Breadcrumb>
                <BreadcrumbItem to="/">首页</BreadcrumbItem>
                <BreadcrumbItem>查询表格</BreadcrumbItem>
            </Breadcrumb>
        </div>
        <div class="page_content">
            <div style="padding:24px;">
                <Row>
                    <div class="fl mr40">
                        订单号码 <Input placeholder="订单号码" style="width:200px;"/>
                    </div>
                    <div class="fl mr40">
                        订单状态 <Input placeholder="订单状态" style="width:200px;"/>
                    </div>
                    <div class="fl mr40">
                        <Input placeholder="请输入姓名搜搜..." style="width:200px;"/>
                    </div>
                    <div class="fl mr40">
                        <Button type="primary" icon="search">搜索</Button>
                        <Button type="ghost" >取消</Button>
                    </div>
                </Row>
                <Row style="margin-top:20px;">
                    <Button type="primary" icon="plus">新建</Button>
                    <Button type="primary" icon="delete">删除</Button>
                    <Button type="primary" icon="search">修改</Button>
                    <Button type="primary" icon="search">查询</Button>
                </Row>
                <div style="margin-top:20px;">
                    <Table :data="tableData1" :columns="tableColumns1" stripe></Table>
                </div>
                <div style="margin:20px 0;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="100" :current="1" @on-change="changePage"></Page>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                tableData1: this.mockTableData1(),
                tableColumns1: [
                    {
                        title: 'Name',
                        key: 'name'
                    },
                    {
                        title: 'Status',
                        key: 'status',
                        render: (h, params) => {
                            const row = params.row
                            const color = row.status === 1 ? 'blue' : row.status === 2 ? 'green' : 'red'
                            const text = row.status === 1 ? 'Working' : row.status === 2 ? 'Success' : 'Fail'

                            return h('div', {
                                props: {
                                    type: 'dot',
                                    color: color
                                }
                            }, text)
                        }
                    },
                    {
                        title: 'Portrayal',
                        key: 'portrayal',
                        render: (h, params) => {
                            return h('Poptip', {
                                props: {
                                    trigger: 'hover',
                                    title: params.row.portrayal.length + 'portrayals',
                                    placement: 'bottom'
                                }
                            }, [
                                h('div', params.row.portrayal.length),
                                h('div', {
                                    slot: 'content'
                                }, [
                                    h('ul', this.tableData1[params.index].portrayal.map(item => {
                                        return h('li', {
                                            style: {
                                                textAlign: 'center',
                                                padding: '4px'
                                            }
                                        }, item)
                                    }))
                                ])
                            ])
                        }
                    },
                    {
                        title: 'People',
                        key: 'people',
                        render: (h, params) => {
                            return h('Poptip', {
                                props: {
                                    trigger: 'hover',
                                    title: params.row.people.length + 'customers',
                                    placement: 'bottom'
                                }
                            }, [
                                h('div', params.row.people.length),
                                h('div', {
                                    slot: 'content'
                                }, [
                                    h('ul', this.tableData1[params.index].people.map(item => {
                                        return h('li', {
                                            style: {
                                                textAlign: 'center',
                                                padding: '4px'
                                            }
                                        }, item.n + '：' + item.c + 'People')
                                    }))
                                ])
                            ])
                        }
                    },
                    {
                        title: 'Sampling Time',
                        key: 'time',
                        render: (h, params) => {
                            return h('div', 'Almost' + params.row.time + 'days')
                        }
                    },
                    {
                        title: 'Updated Time',
                        key: 'update',
                        render: (h, params) => {
                            return h('div', this.formatDate(this.tableData1[params.index].update))
                        }
                    }
                ]
            }
        },
        methods: {
            mockTableData1 () {
                let data = []
                for (let i = 0; i < 10; i++) {
                    data.push({
                        name: 'Business' + Math.floor(Math.random() * 100 + 1),
                        status: Math.floor(Math.random() * 3 + 1),
                        portrayal: ['City', 'People', 'Cost', 'Life', 'Entertainment'],
                        people: [
                            {
                                n: 'People' + Math.floor(Math.random() * 100 + 1),
                                c: Math.floor(Math.random() * 1000000 + 100000)
                            },
                            {
                                n: 'People' + Math.floor(Math.random() * 100 + 1),
                                c: Math.floor(Math.random() * 1000000 + 100000)
                            },
                            {
                                n: 'People' + Math.floor(Math.random() * 100 + 1),
                                c: Math.floor(Math.random() * 1000000 + 100000)
                            }
                        ],
                        time: Math.floor(Math.random() * 7 + 1),
                        update: new Date()
                    })
                }
                return data
            },
            formatDate (date) {
                const y = date.getFullYear()
                let m = date.getMonth() + 1
                m = m < 10 ? '0' + m : m
                let d = date.getDate()
                d = d < 10 ? ('0' + d) : d
                return y + '-' + m + '-' + d
            },
            changePage () {
                // The simulated data is changed directly here, and the actual usage scenario should fetch the data from the server
                this.tableData1 = this.mockTableData1()
            }
        }
    }
</script>


